#home-slider {
  background-color: #fff;
  width: 100%;
  height: auto;
  position: relative;
}
#home-slider img {
  width: 100%;
  height: 150px;
}
.home-slider-point {
  position: absolute;
  bottom: 0;
  left: 40%;
}
.home-slider-point ul {
  width: 100%;
  height: auto;
  text-align: center;
}
.home-slider-point li {
  list-style: none;
  display: inline-block;
  height: 8px;
  width: 8px;
  border-radius: 4px;
  background-color: #ccc;
  margin: 0 3px;
}
.home-slider-point li.selected {
  background-color: #e9203d;
}



