.singerListType-left-ul {
  float: left;
  font-size: 14px;
  color: #2c2c2c;
  width: 35%;
}
.singerListType-left-ul li {
  padding: 15px;
  border-bottom: 1px solid #ccc;
}
.singerListType-left-ul li span {
  padding: 3px;
}
.singerListType-left-ul li:hover {
  border-left: 2px solid #d93929;
}
.singerListType-right-ul {
  width: 65%;
  float: right;
}
.singerListType-right-ul li {
  margin-top: 5px;
  position: relative;
}
.singerListType-right-ul li .person {
  border-radius: 50%;
  width: 20%;
  height: 50px;
}
.singerListType-right-ul li span {
  position: absolute;
  padding-left: 40px;
  top: 15px;
}
.singerListType-right-ul li .arrow {
  width: 6%;
  position: absolute;
  right: 10px;
  top: 20px;
}



