.tab {
  height: 44px;
  line-height: 44px;
  font-size: 14px;
  background: #fff;
  width: 100%;
}
.tab .tab_item {
  position: relative;
  text-align: center;
  padding-bottom: 7px;
}
.tab .tab_item .tab_link {
  padding-bottom: 5px;
  color: #2c2c2c;
  margin: 10%;
}
.tab .tab_item.selected {
  color: #d93929;
  border-bottom: 1px solid #d93929;
}



