#home-header {
  background-color: #e9203d;
  padding-top: 15px;
  padding-bottom: 15px;
}
#home-header .home-header-input {
  border-radius: 10px;
  text-align: center;
  position: relative;
}
#home-header .home-header-input .icon-search {
  position: absolute;
  color: #ccc !important;
  left: 11%;
  top: 25%;
}
#home-header .home-header-input .search-input {
  border: 0;
  width: 80%;
  border-radius: 10px;
  padding: 5px;
  text-indent: 1.7em;
}



