@font-face {
  font-family: 'icomoon';
  src: url('../fonts/icomoon.eot?xiyoi4');
  src: url('../fonts/icomoon.eot?xiyoi4#iefix') format('embedded-opentype'), url('../fonts/icomoon.ttf?xiyoi4') format('truetype'), url('../fonts/icomoon.woff?xiyoi4') format('woff'), url('../fonts/icomoon.svg?xiyoi4#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
}
[class^="icon-"],
[class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.icon-music:before {
  content: "\e911";
}
.icon-search:before {
  content: "\e986";
}
.icon-menu:before {
  content: "\e9bd";
}



