.rankList {
  position: relative;
}
.rankList .all {
  position: absolute;
  top: 10px;
  border-left: 2px solid #d93929;
  font-size: 16px;
  color: #1B1919;
  padding-left: 15px;
}
.rankList .rankList-ul {
  padding-top: 30px;
}
.rankList .rankList-ul .rankList-li {
  position: relative;
}
.rankList .rankList-ul .rankList-li .rankList-img {
  padding: 10px;
  width: 40%;
}
.rankList .rankList-ul .rankList-li .title {
  font-size: 14px;
  color: #686363;
  position: absolute;
  top: 5px;
  right: 19px;
}
.rankList .rankList-ul .rankList-li .title .firstTitle {
  padding-top: 4px;
}
.rankList .rankList-ul .rankList-li .title .subTitle {
  padding-top: 10px;
}
.rankList .rankList-ul .rankList-li .title .thirdTitle {
  padding-top: 10px;
}
.rankList .rankList-ul .rankList-li .title .lastTitle {
  padding-top: 10px;
}
.rankList .rankList-ul .rankList-li .detail {
  color: white;
  font-size: 14px;
  position: absolute;
  left: 22px;
  bottom: 18px;
}



