.search-head {
  background-color: #e9203d;
  padding-top: 15px;
  padding-bottom: 15px;
  position: relative;
}
.search-head .search-head-left {
  float: left;
  position: absolute;
  top: 31px;
  left: 11px;
}
.search-head .search-head-left .search-arrowleft {
  width: 64%;
}
.search-head .search-head-input {
  position: absolute;
  border-radius: 10px;
  text-align: center;
  position: relative;
  width: 90%;
  left: 10%;
  right: 10px;
}
.search-head .search-head-input .suggest {
  position: relative;
}
.search-head .search-head-input .suggest .search-input {
  border-bottom: #fff 1px solid;
  border-left-width: 0px;
  border-right-width: 0px;
  border-top-width: 0px;
  width: 100%;
  background-color: #e9203d;
  padding: 10px;
  font-size: 18px;
  color: #fff;
}
.search-head .search-head-input .suggest .option {
  width: 100%;
  background-color: #FFFFFF;
  position: absolute;
  left: 0;
}
.search-head .search-head-input .icon-search {
  position: absolute;
  color: #ccc !important;
  left: 11%;
  top: 25%;
}



