.playPage .playPage-div {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 150;
  background: #aaa;
}
.playPage .playPage-div .playPage-bg {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  z-index: -1;
  width: 100%;
  background-size: cover;
  background-position: bottom center;
  opacity: 0.6;
  -webkit-transition: opacity .3s linear;
  -o-transition: opacity .3s linear;
  transition: opacity .3s linear;
}
.playPage .playPage-div .playPage-bg:after {
  content: " ";
  background-color: rgba(0, 0, 0, 0.5);
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
}
.playPage .playPage-div .coverImg {
  position: absolute;
  width: 48%;
  top: 34%;
  left: 24%;
  text-align: center;
  -webkit-animation: water 18s infinite linear;
          animation: water 18s infinite linear;
  -webkit-animation-play-state: paused;
          animation-play-state: paused;
}
.playPage .playPage-div .coverImg.active {
  -webkit-animation-play-state: running;
          animation-play-state: running;
}
@-webkit-keyframes water {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes water {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
.playPage .playPage-div .jiaopian {
  width: 75%;
  position: absolute;
  text-align: center;
  left: 10%;
  top: 26%;
}
.playPage .playPage-div .needle {
  position: absolute;
  width: 30%;
  right: 30%;
  top: 10%;
  -webkit-transform: rotate(-20deg);
      -ms-transform: rotate(-20deg);
          transform: rotate(-20deg);
  -webkit-transition: all .3s linear;
  -o-transition: all .3s linear;
  transition: all .3s linear;
  -webkit-transform-origin: 8px 0;
      -ms-transform-origin: 8px 0;
          transform-origin: 8px 0;
}
.playPage .playPage-div .needle.active {
  -webkit-transform: rotate(0deg);
      -ms-transform: rotate(0deg);
          transform: rotate(0deg);
}
.playPage .playPage-div .singer-div {
  height: 60px;
  width: 100%;
  border-bottom: 1px solid #7E7C7C;
  color: white;
  position: relative;
}
.playPage .playPage-div .singer-div .arrowLeft {
  width: 3%;
  position: absolute;
  left: 10px;
  top: 17px;
}
.playPage .playPage-div .singer-div .singer {
  position: absolute;
  left: 43px;
  top: 20px;
}
.playPage .playPage-div .singer-div .share {
  position: absolute;
  width: 12%;
  right: 7px;
  top: 10px;
}
.playPage .playPage-div .btn {
  position: relative;
  left: 37%;
  top: 77%;
}
.playPage .playPage-div .btn .play {
  position: absolute;
  width: 20%;
}
.playPage .playPage-div .btn .pause {
  position: absolute;
  width: 20%;
}
.playPage .playPage-div .btn .pause {
  display: none;
}
.playPage .playPage-div .btn.active .pause {
  display: block;
}
.playPage .playPage-div .btn.active .play {
  display: none;
}
.playPage .playPage-div .prev {
  position: absolute;
  width: 15%;
  top: 78%;
  left: 14%;
}
.playPage .playPage-div .next {
  position: absolute;
  width: 15%;
  top: 78%;
  right: 23%;
}



