body {
  margin: 0;
  padding: 0;
  background-color: #f1f1f1;
}
body a {
  text-decoration: none;
  color: #2c2c2c;
}
body li {
  list-style: none;
}
body * {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-family: "微软雅黑", "Times New Roman", Georgia, Serif;
}
input {
  outline: none;
}
.float-left {
  float: left;
}
.float-right {
  float: right;
}
.clear-fix:after {
  display: table;
  content: '';
  clear: both;
}



