.home-playList {
  padding-top: 10px;
}
.home-playList .homePlayList-header {
  border-left: 3px solid #bb2c08;
  padding-left: 10px;
}
.home-playList .homePlayList-header span {
  font-size: 18px;
}
.home-playList .homePlayList-header img {
  width: 6%;
  padding-left: 5px;
}
.home-playList .homePlayList-container {
  width: 100%;
}
.home-playList .homePlayList-container ul {
  margin-top: 5px;
}
.home-playList .homePlayList-container ul li {
  list-style: none;
  float: left;
  width: 30%;
  margin: 5px;
}
.home-playList .homePlayList-container ul li img {
  display: inline-block;
  width: 100%;
  height: 90px;
}
.home-playList .homePlayList-container ul li span {
  display: block;
  width: 100%;
  font-size: 12px;
}



